<template>
  <Modal
    v-model="isShow"
    title="通话中"
    :loading="true"
    @on-ok="close"
    @on-cancel="close"
    ok-text="结束通话"
    cancel-text="">
    <Row>
      <Col span="10">
        <div id="local-stream"></div>
      </Col>
      <Col span="4">
        <Divider />
      </Col>
      <Col span="10">
        <div id="remote-stream"></div>
      </Col>
    </Row>
    <Row>
      <Col>
        视频开关 <i-switch v-model="openVideo" @on-change="onVideoOpened">
        <span slot="open">开</span>
        <span slot="close">关</span>
      </i-switch>
      </Col>
    </Row>
    <Row>
      <Col>
        音频开关 <i-switch v-model="openAudio" @on-change="onAudioOpened">
        <span slot="open">开</span>
        <span slot="close">关</span>
      </i-switch>
      </Col>
    </Row>
  </Modal>
</template>

<script>
import random from "../utils/Random";
export default {
  name: "DevicesCheck",
  props: {
    close: Function,
    show: Boolean
  },
  data() {
    return {
      isShow: false,
      openVideo: false,
      openAudio: false
    }
  },
  watch: {
    show(val) {
      this.isShow = val;
      if (this.isShow) {
        this.onVideoCall();
      } else {
        this.onVideoCallDone();
      }
    }
  },
  mounted() {
  },
  methods: {
    onVideoCall: function () {
      let that = this;
      console.log("onVideoCall");
      that.openVideo = that.$store.state.hasVideo;
      that.openAudio = that.$store.state.hasAudio;
      // const roomId = random(100000, 999999);
      const roomId = 999999;
      that.$store.state.client.join(roomId, function (type, stream) {
        // that.$Notice.open({
        //   title: "消息提示",
        //   desc: "type"
        // });
        console.log("事件类型", type, stream);
        switch (type) {
          case 'local':
            console.log("add local stream");
            stream.play('local-stream');
            that.$store.state.client.publish();
            break;
          case 'subscribed':
            console.log("add remote stream");
            stream.play('remote-stream');
            break;
          default:
            break;
        }
      });
    },
    onVideoCallDone: function () {
      console.log("onVideoCallDone");
      this.$store.state.client.leave();
    },
    onVideoOpened: function () {
      if (this.openVideo) {
        this.$store.state.client.unMuteVideo()
      } else {
        this.$store.state.client.muteVideo()
      }
    },
    onAudioOpened: function () {
      if (this.openAudio) {
        this.$store.state.client.unMuteAudio()
      } else {
        this.$store.state.client.muteAudio()
      }
    }
  }
}
</script>

<style scoped>
#local-stream{
}
#remote-stream{
}
</style>



